@import "../../../SCSS/index.scss";

.how-my-courses-work{
    margin-top: 100px;
    margin-bottom: 50px;

    &-title{
        text-align: center;
        line-height: 0.7;
        margin-bottom: 40px;

        h2{
            color:$font-light;
            font-size: 32px;
            font-weight: bold;
            text-transform: uppercase;
        }

        h3{
            color:$primary-color-light;
        }
    }

    .row-cards{
        .ant-col{
            padding: 10px !important;
            
        }  
    }

    &-card{
        border: 0;
        background-color:$backgrounnd-dark-light-web;
        overflow: hidden;
        text-align: center;
        border-radius: 10px;
        box-shadow: 0px 0px 10px 2px rgba(0,0,0,0.75) ;
        -webkit-box-shadow: 0px 0px 10px 2px rgba(0,0,0,0.75) ;
        -moz-box-shadow: 0px 0px 10px 2px rgba(0,0,0,0.75) ;

        .ant-card-body{
            min-height: 150px;
        }

        svg{
            font-size: 45px;
            color: $primary-color-light;
        
             }

        .ant-card-meta-detail{
            .ant-card-meta-title{
                color: $primary-color-light;
                font-size: 16px;
                font-weight: bold;
            }
            .ant-card-meta-description{
                color: $primary-color-light;
          }

        }
       
    }
}